*{
    font-family: helvetica,Arial, sans-serif;
}
body{
    padding: 20px;
}
input{
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}
.card_grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    padding: 1%;
    overflow: hidden;
}
.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 170px;
}